import React from "react";
import ArchitectureComponent from './content/architecture';

const LoaderComponent = (props) => {

  if (props.showPopup === true) {

    switch(props.content.toLowerCase())
    {
      case "architecture":
        return <div className='popout-content container d-block'>
          <ArchitectureComponent />
        </div>;
    }

  }



  return '';
};

export default LoaderComponent;