import React from "react";
import iconRender from '../../helpers/icon-render.helper';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';

const ArchitectureComponent = () => {

  return <React.Fragment>
    <FontAwesomeIcon icon={faTimes}/>
    {iconRender({name: "Architecture"})}

    <h2>Snapshot</h2>

    <p>Snapshot was a product that I designed, built and released. The system is designed to take information from end points and compare their state to obtain information on outages, etc. This was a SaaS product designed to run 
      on Azure serverless functions. The architectural idea here was to create a scaleable single tenant microservice system. Some of the contraints we had to think about were:
    </p>
    <ul>
      <li>Load was a virtual unknown from the outset. We needed to be able to scale to high throughput but we also needed it to be cost effective and efficient.</li>
      <li>We need to rate limit so that we did not effect downstream service (specifically <a href="https://www.ivanti.co.uk/products/ivanti-neurons-edge-intelligence">Edge Intelligence</a> where we were obtaining our data from</li>
      <li>We needed the data to be timely and as a close to an incident as possible</li>
    </ul>
  </React.Fragment>;

}

export default ArchitectureComponent;